@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Raleway:wght@400;700&display=swap");
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
.flex {
  display: flex;
}

.grid {
  display: grid;
}

.jc-c {
  justify-content: center;
}

.ai-c {
  align-items: center;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: #070439;
}

button,
input {
  font-family: "Raleway", sans-serif;
}

button {
  font-weight: 700;
}

input {
  font-weight: 400;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #070439;
  margin: 0;
  padding: 0;
}

.header nav {
  padding: 1.5rem;
  margin-bottom: 4.625rem;
  justify-content: space-between;
}
@media (min-width: 53.125em) {
  .header nav {
    padding-inline: 3.75rem;
    padding-block: 2.5rem;
    margin-bottom: 2.3125rem;
  }
}
.header__logo img {
  width: 5rem;
  height: 1.5625rem;
}
@media (min-width: 53.125em) {
  .header__logo img {
    width: 10rem;
    height: auto;
  }
}
.header__links a {
  font-size: 0.75rem;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  transition: all 350ms ease-in-out;
}
.header__links a:hover {
  color: #3065f8;
}
@media (min-width: 53.125em) {
  .header__links a {
    font-size: 1rem;
  }
}
.header__links a:not(:first-child) {
  margin-left: 1.5625rem;
}
@media (min-width: 53.125em) {
  .header__links a:not(:first-child) {
    margin-left: 4.875rem;
  }
}

.primary {
  flex-flow: column nowrap;
  margin-bottom: 5.625rem;
}
@media (min-width: 53.125em) {
  .primary {
    flex-flow: row-reverse nowrap;
    gap: 8rem;
    padding-inline: 3.75rem;
  }
}
.primary__hero {
  padding-inline: 1.875rem;
  margin-bottom: 3.125rem;
}
.primary__hero img {
  width: 100%;
}
@media (min-width: 53.125em) {
  .primary__hero {
    flex: 1 1 50%;
    padding: 0;
  }
  .primary__hero img {
    max-width: 43.75rem;
  }
}
.primary__container {
  text-align: center;
}
@media (min-width: 53.125em) {
  .primary__container {
    flex: 1 1 50%;
    align-self: flex-start;
    text-align: left;
    margin-block: 4.6875rem;
  }
  .primary__container > h1, .primary__container p {
    padding: 0;
  }
  .primary__container .primary__fill-out {
    padding: 0;
  }
}
.primary__title {
  font-size: 1.5625rem;
  font-weight: 700;
  line-height: 1.5;
  padding-inline: 1.875rem;
  margin-bottom: 2rem;
}
@media (min-width: 53.125em) {
  .primary__title {
    font-size: min(max(1rem, 3.5vw), 2.5rem);
    margin-bottom: 1.625rem;
  }
}
.primary__subtitle {
  font-size: 0.875rem;
  font-weight: 400;
  padding-inline: 2.625rem;
  margin-bottom: 2.125rem;
}
.primary__fill-out {
  padding-inline: 2.625rem;
}
.primary__fill-out form {
  flex-flow: column nowrap;
  gap: 0.75rem;
}
@media (min-width: 53.125em) {
  .primary__fill-out form {
    flex-flow: row nowrap;
    margin-right: 4.6875rem;
  }
}
.primary__cta {
  font-size: 0.875rem;
  transition: all 350ms ease-in-out;
}
.primary__cta:hover {
  cursor: pointer;
  background-color: rgba(48, 101, 248, 0.842);
}
@media (min-width: 53.125em) {
  .primary__cta {
    font-size: 0.75rem;
  }
  .primary__cta.primary__cta {
    width: 13.75rem;
  }
}

.feature {
  flex-flow: column nowrap;
  background-color: #f6f6fe;
  clip-path: ellipse(125% 70% at 50% 70%);
}
@media (min-width: 53.125em) {
  .feature {
    flex-flow: row-reverse nowrap;
    gap: 6rem;
    padding-block: 7.5rem;
    padding-inline: 3.75rem;
  }
}
.feature__hero {
  padding-inline: 1.625rem;
  margin-top: 5.875rem;
  margin-bottom: 5.625rem;
}
.feature__hero img {
  width: 100%;
}
@media (min-width: 53.125em) {
  .feature__hero {
    display: flex;
    flex: 1 1 50%;
    justify-content: center;
    padding: 0;
    margin: 0;
  }
  .feature__hero img {
    max-width: 43.75rem;
  }
}
@media (min-width: 53.125em) {
  .feature__container {
    flex: 1 1 50%;
  }
}
.feature__heading {
  text-align: center;
  color: #070439;
  margin-bottom: 1.875rem;
}
@media (min-width: 53.125em) {
  .feature__heading {
    font-size: min(max(1rem, 3.5vw), 2.5rem);
    text-align: left;
    padding: 0;
  }
}
.feature__text {
  padding-inline: 1.25rem;
  margin-bottom: 1.25rem;
  color: #070439;
}
@media (min-width: 53.125em) {
  .feature__text {
    text-align: left;
    padding: 0;
  }
}
.feature__text:nth-of-type(2) {
  margin-bottom: 2.5rem;
}
.feature__link {
  display: block;
  margin-bottom: 2.875rem;
  text-align: center;
}
.feature__link a {
  display: inline-block;
  border-bottom: 1px solid #3c9f8f;
  color: #3c9f8f;
}
.feature__link a > img {
  display: inline;
  vertical-align: middle;
}
@media (min-width: 53.125em) {
  .feature__link {
    display: inline-block;
  }
}
.feature__testimonial {
  background-image: url(/app/asset/images/icon-quotes.svg);
  background-repeat: no-repeat;
  background-position-x: 1.625rem;
  background-position-y: 1.625rem;
  background-color: white;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.24);
  border-radius: 0.25rem;
  padding-block: 1.25rem;
  padding-inline: 1.625rem;
  margin-inline: 3.125rem;
  margin-bottom: 5rem;
}
.feature__testimonial > p {
  font-size: 10px;
  margin-top: 1.375rem;
  margin-bottom: 1rem;
}
@media (min-width: 53.125em) {
  .feature__testimonial {
    width: 21.875rem;
    height: 12.5rem;
    margin: 0;
    background-position-y: 1.4375rem;
  }
  .feature__testimonial > p {
    font-size: 0.875rem;
  }
}
.feature__user {
  flex-flow: row nowrap;
}
.feature__user img {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
}
.feature__user > div {
  flex-flow: column nowrap;
  margin-left: 0.75rem;
}
.feature__username {
  font-size: 0.625rem;
  font-weight: 700;
}
.feature__user-status {
  font-size: 0.5rem;
}

.footer {
  padding-block: 3.75rem;
  padding-inline: 3.75rem;
  background-color: #070439;
  color: #f6f6fe;
}
.footer__container {
  display: flex;
  align-items: flex-end;
}
@media (min-width: 53.125em) {
  .footer__container {
    padding-block: 1.5rem;
  }
}
.footer__logo {
  display: block;
}
.footer__logo path {
  fill: #f6f6fe;
}
.footer__content {
  flex-flow: column nowrap;
}
@media (min-width: 53.125em) {
  .footer__content {
    flex-flow: row nowrap;
    align-items: flex-start;
    gap: 7rem;
  }
}
.footer__contact {
  flex: 1 1 33%;
  flex-flow: column nowrap;
  justify-content: flex-start;
  gap: 0.625rem;
  margin-top: 2.5rem;
  margin-bottom: 3.75rem;
}
.footer__contact p, .footer__contact img {
  display: inline;
}
.footer__contact img {
  vertical-align: middle;
  margin-right: 0.3125rem;
}
@media (min-width: 53.125em) {
  .footer__contact {
    margin: 0;
  }
}
.footer__links {
  flex-flow: column nowrap;
  gap: 2rem;
}
@media (min-width: 53.125em) {
  .footer__links {
    flex: 1 1 33%;
    flex-flow: row nowrap;
    align-items: flex-end;
    justify-content: space-between;
    gap: 0;
  }
}
.footer__box {
  flex-flow: column nowrap;
  gap: 0.5rem;
}
.footer__box a {
  color: #f6f6fe;
  transition: all 350ms ease-in-out;
}
.footer__box a:hover {
  color: #3065f8;
}
.footer__social {
  margin-top: 3.25rem;
}
.footer__social ul {
  padding: 0;
  margin: 0;
}
.footer__social li {
  transition: all 350ms ease-in-out;
}
.footer__social li:hover {
  border: 1px solid #3065f8;
  color: #3065f8;
}
@media (min-width: 53.125em) {
  .footer__social {
    flex: 1 1 33%;
    margin-top: 0;
  }
}

.access {
  flex-flow: column nowrap;
  padding-top: 3.75rem;
  padding-bottom: 4.375rem;
  padding-inline: 2.25rem;
  background-color: #585989;
  color: #f6f6fe;
  text-align: center;
}
@media (min-width: 53.125em) {
  .access {
    flex-flow: row nowrap;
    gap: 12rem;
    text-align: left;
    padding: 0;
  }
}
.access__container {
  flex: 1 1 50%;
}
@media (min-width: 53.125em) {
  .access__container {
    padding-block: 6.25rem;
    padding-inline: 3.75rem;
    width: 50%;
  }
}
.access__heading {
  font-size: 1.125rem;
  font-weight: 700;
}
@media (min-width: 53.125em) {
  .access__heading {
    font-size: 1.875rem;
  }
}
.access__text {
  font-size: 0.9375rem;
  margin-block: 1.375rem;
}
@media (min-width: 53.125em) {
  .access__text {
    font-size: 1rem;
  }
}
.access__fill-out {
  padding-inline: 1.5625rem;
}
.access__fill-out form {
  display: flex;
  flex-flow: column nowrap;
  gap: 0.75rem;
}
@media (min-width: 53.125em) {
  .access__fill-out form {
    flex: 1 1 50%;
    justify-content: flex-start;
    margin-right: 4.6875rem;
  }
}
@media (min-width: 53.125em) {
  .access__fill-out {
    padding-block: 6.25rem;
    padding-inline: 3.75rem;
    width: 50%;
  }
}
.access__cta {
  font-size: 0.875rem;
  transition: all 350ms ease-in-out;
}
.access__cta:hover {
  cursor: pointer;
  background-color: rgba(48, 101, 248, 0.842);
}
@media (min-width: 53.125em) {
  .access__cta {
    font-size: 0.75rem;
  }
}

.input {
  width: 100%;
  height: 2.5rem;
  outline-color: rgba(48, 101, 248, 0.63);
  box-shadow: 4px 4px 27px rgba(0, 0, 0, 0.2);
  padding: 0.75rem;
}
.input::placeholder {
  font-size: 0.75rem;
}

.button {
  height: 2.5rem;
  border: 0 solid none;
  border-radius: 0.25rem;
  background-color: #3065f8;
  box-shadow: 0 8px 27px rgba(0, 0, 0, 0.24);
  color: white;
}
@media (max-width: 53.0625em) {
  .button {
    width: 100%;
  }
}
@media (min-width: 53.125em) {
  .button {
    width: max-content;
    padding-inline: 2rem;
  }
}

.social {
  font-size: 0.875rem;
  border-radius: 50%;
  border: 1px solid #fff;
  width: 1.875rem;
  height: 1.875rem;
  color: #fff;
  margin: 0 0.5rem;
}